// Slick Slider
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
  & .slick-track,
  & .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
  &t:focus {
    outline: none;
  }
  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  &:before,
  &:after {
    display: table;
    content: '';
  }
  &:after {
    clear: both;
  }
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: none;
  /*height: 100%;*/
  min-height: 1px;
  & img {
    display: block;
  }
  &.slick-loading img {
    display: none;
  }
  &.dragging img {
    pointer-events: none;
  }
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-initialized .slick-slide {
  display: flex;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.featured-slider {
  color: $color-white;
  font-size: $base-size;
  @include media-breakpoint-down(xs) {
    //ont-size: 8px;
  }
  @include media-breakpoint-only(sm) {
    //font-size: 12px;
  }

  h2{
    font-size: inherit;
  }

  .slide {
    .fullwidth {
      background-repeat: no-repeat;
      background-size: cover;
      padding-bottom: 9.063em;
      padding-top: 9.375em;

      @include media-breakpoint-down(sm) {
        padding-bottom: 3em;
        padding-top: 3em;
      }

    }
  }

  .slider-title{

    text-transform: uppercase;
    margin-top: 0;

    @include media-breakpoint-down(xs) {
      text-align: center;
    }
    //font-size: $base-size;

    .title-part{
      color: $color-white;
      font-family: $bold-font;
      font-size: 5em;
      line-height: 0.9;
      margin-bottom: 0;
      margin-top: 0;
      display: block;
      @include media-breakpoint-down(xs) {
        text-align: center;
        font-size: 2em;
      }
      img {
        float: left;
        max-width: 78px;
        margin-right: .25em;
        @include media-breakpoint-down(xs) {
          max-width: 45px;

        }
      }
    }

    .subtitle-part{

      color: $color-white;
      font-family: $medium-font;
      font-size: 4em;
      line-height: 0.9;
      margin-bottom: 24px;
      margin-top: 0;
      display: block;

      @include media-breakpoint-down(sm) {
        font-size: 2em;
        display: flex;
        align-items: center;
      }

      @include media-breakpoint-down(xs) {
        text-align: center;
        justify-content: center;
        font-size: 1.5em;
      }
      &.light {
        font-family: $light-font;
      }

      .slider-title-text{
        @include media-breakpoint-down(xs) {

          text-align: left;

        }
      }

      img {
        float: left;
        max-width: 78px;
        margin-right: .25em;
        @include media-breakpoint-down(xs) {
          max-width: 45px;
          float: none;
          display: flex;
        }
      }

    }

  }

  & .slider-text {
    margin-bottom: 12px;
    font-size: 1.5em;
    @include media-breakpoint-down(xs) {
      text-align: center;
      font-size: 1.2em;
      line-height: 1.2;
    }
  }
  & .btn {
    @include media-breakpoint-up(sm) {
      margin-top: 50px;
    }
  }
  .slider-extra-img {
    img {
      display: block;
      max-width: 100%;
      height: auto;
      margin-left: auto;
      margin-right: auto;


      @include media-breakpoint-down(xs) {
        /*max-width: 280px;*/
        max-height: 200px;
        margin-left: auto;
        margin-right: auto;
      }
      @include media-breakpoint-up(lg) {
        /*position: absolute;
        top: 48px;*/
        max-height: 424px;
        margin: 0 0 0 auto;
      }
    }
  }

  .d-flex-row{
    display: flex;

    @include media-breakpoint-down(xs) {
      flex-direction: column;
    }
  }

  .d-flex{
    display: flex;
    float: none;
    flex-direction: column;
    justify-content: flex-end;
  }


}