@import 'fancybox';

.entry-gallery {

  .d-flex-row{
    display: flex;
    flex-wrap: wrap;

    figure {
      display: flex;
      flex-direction: column;
    }
  }


  figure {
    margin-bottom: $section-padding;


    &.single-gallery{
      margin-bottom: $elements-gap;
    }


    img {
      display: block;
      height: auto;
      max-width: 100%;
      @include media-breakpoint-down(xs) {
        margin-left: auto;
        margin-right: auto;
      }

      &.box-shadow{
        @include box-shadow();
      }


    }
    figcaption {
      color: $dark-color;
      font-size: $base-size * 1.5;
      line-height: 1.1;
      margin-top: $base-size * 1.5;
      @include media-breakpoint-down(md) {
        font-size: $base-size;
      }
    }

    .gallery-link{
      @extend figcaption;

      &:hover{
        color: $secondary-color;
        text-decoration: none;
      }

    }

  }
}

.gallery-text{
  margin-top: $section-padding;
  font-size: 18px;
  /*font-family: "ProximaNova";*/
  line-height: 1.4;
}

.gallery-btn-row{
  margin-bottom: $section-padding;

}

